strong {
  @include FFRB; }

.row {
  &:after {
    content: '';
    display: table;
    clear: both; }

  .col-6 {
    width: 50%;
    float: left;
    &:nth-child(1) {
      padding-right: 15px; }
    &:nth-last-child(1) {
      padding-left: 15px; } } }

h2 {
  @include FFRSR;
  font-size: 24px;
  color: #4b4e4b;
  text-align: center;
  &:after {
    content: '';
    display: block;
    height: 4px;
    margin-top: 9px;
    background: url(../images/h2-bg.png) no-repeat center;
    margin-bottom: 39px; } }

.about {
  font-size: 14px;
  line-height: 24px;
  color: #3d3d3d;
  p {
    padding-bottom: 25px; } }

.news-list {
  &__item {
    padding-left: 38px;
    margin-bottom: 35px;
    &-date {
      float: left;
      width: 102px;
      height: 101px;
      background: url(../images/date-bg.png) no-repeat;
      text-align: center;
      font-size: 12px;
      line-height: 14.19px;
      color: #ffffff;
      padding: 4px 17px 24px 14px;
      span {
        margin-top: 4px;
        font-size: 36px;
        line-height: 36px;
        display: block;
        margin-bottom: 5px;
        @include FFRSB; } }
    &-title {
      font-size: 15px;
      line-height: 18px;
      color: #3d3d3d;
      margin-bottom: 20px;
      @include FFRB; }
    &-cont {
      margin-left: 108px;
      color: #3d3d3d;
      font-size: 14px;
      line-height: 18px; }
    &-link {
      margin-top: 15px;
      a {
        color: #2db878;
        text-decoration: none;
        &:after {
          content: '';
          width: 6px;
          height: 10px;
          display: inline-block;
          margin-left: 7px;
          background: url(../images/link-icon.png) no-repeat; } } } } }
